<template>
  <div id="loading-container" class="vs-con-loading__container">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <form-wizard ref="wizard"
                   color="rgba(var(--vs-primary), 1)"
                   :title="null" :subtitle="null"
                   :nextButtonText="$t('NEXT')"
                   :backButtonText="$t('PREVIOUS')">

        <template slot="custom-buttons-left" slot-scope="props">
          <vs-button class="mr-2" color="primary" type="border" @click="cancel" v-if="props.activeTabIndex === 0">{{ $t('CANCEL') }}</vs-button>
        </template>
        <template slot="finish">
          <div class="dropdown-button-container">
            <vs-button class="btnx" type="filled" @click="createTransferType('browse')">{{ $t('SUBMIT') }}</vs-button>
            <vs-dropdown vs-trigger-click>
              <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
              <vs-dropdown-menu>
                <vs-dropdown-item @click="createTransferType('stay')">
                  <span>{{ $t('SUBMIT_STAY') }}</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="createTransferType('view')">
                  <span>{{ $t('SUBMIT_VIEW') }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </template>

        <tab-content :title="$t('GENERAL')" icon="far fa-tag">
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('TYPE_NAME') }}</label>
              <vs-input class="w-full" v-model="typeName"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('PARENT_TYPE') }}</label>
              <funds-transfer-type-lookup :title="$t('PARENT_TYPE')" valueField="typeId" displayField="typeName" @select-record="selectTransferTypeRecord"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('TYPE_BEHAVIOUR') }}</label>
              <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="typeBehaviourEnumId">
                <vs-select-item v-for="(enumeration, index) in typeBehaviours" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
              </vs-select>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('EXTERNAL_ID') }}</label>
              <vs-input class="w-full" v-model="externalTypeId"/>
            </div>
          </div>
          <div class="nv-row mb-4" v-if="typeBehaviourEnumId !== 'FT_TYPE_PLACEHOLDER'">
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('DEBIT_FIELD_TYPE') }}</label>
              <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="debitFieldTypeEnumId">
                <vs-select-item v-for="(enumeration, index) in debitFieldTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
              </vs-select>
            </div>
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('CURRENCY_FIELD_TYPE') }}</label>
              <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="currencyFieldTypeEnumId">
                <vs-select-item v-for="(enumeration, index) in currencyFieldTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
              </vs-select>
            </div>
          </div>
          <div class="nv-row mb-4" v-if="typeBehaviourEnumId !== 'FT_TYPE_PLACEHOLDER'">
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('CHARGES_FIELD_TYPE') }}</label>
              <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="chargesFieldTypeEnumId">
                <vs-select-item v-for="(enumeration, index) in chargesFieldTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
              </vs-select>
            </div>
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('CHARGES_AMOUNT_THRESHOLD') }}</label>
              <div class="flex">
                <vs-input-number v-model="chargesAmountThreshold" min="0"/>
              </div>
            </div>
          </div>
          <div class="nv-row mb-4" v-if="bulkTypeBehaviour">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('TEMPLATE_FILE') }}</label>
              <div class="flex">
                <input class="vs-inputx vs-input--input normal" type="file" id="file" ref="file" v-on:change="handleFileUpload()"/>
              </div>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('POSITION') }}</label>
              <div class="flex">
                <vs-input-number v-model="position" min="0"/>
              </div>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <vs-checkbox v-model="disabled">{{ $t('DISABLED') }}</vs-checkbox>
            </div>
          </div>
        </tab-content>

        <tab-content :title="$t('NOTES')" icon="far fa-sticky-note">
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('INPUT_NOTES') }}</label>
              <div class="code-editor">
                <codemirror v-model="inputNotes" :options="vueOptions"></codemirror>
              </div>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('REVIEW_NOTES') }}</label>
              <div class="code-editor">
                <codemirror v-model="reviewNotes" :options="vueOptions"></codemirror>
              </div>
            </div>
          </div>
        </tab-content>
      </form-wizard>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import CodeEditorMixin from '@/mixins/commons/CodeEditorMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import FundsTransferTypeService from '@/services/ft/FundsTransferTypeService'
import EnumerationService from '@/services/commons/EnumerationService'
import FundsTransferTypeLookup from '@/views/ft/funds-transfer-type/FundsTransferTypeLookup'
import { FormWizard, TabContent } from 'vue-form-wizard'

export default {
  name: 'funds-transfer-type-create',
  mixins: [AlertMixin, CodeEditorMixin, LoadingMixin],
  components: {
    FundsTransferTypeLookup,
    FormWizard,
    TabContent
  },
  data: function () {
    return {
      typeName: '',
      parentTypeId: '',
      externalTypeId: '',
      typeBehaviours: [],
      typeBehaviourEnumId: 'FT_TYPE_SINGLE_OWN_ACCT',
      debitFieldTypes: [],
      debitFieldTypeEnumId: 'FT_DEBIT_FLD_ALL',
      currencyFieldTypes: [],
      currencyFieldTypeEnumId: 'FT_CCY_FLD_CREDIT',
      chargesFieldTypes: [],
      chargesFieldTypeEnumId: 'FT_CHARGE_FLD_NA',
      chargesAmountThreshold: 0,
      inputNotes: '',
      reviewNotes: '',
      templateFile: null,
      disabled: false,
      position: 0
    }
  },
  computed: {
    bulkTypeBehaviour: function () {
      return this.typeBehaviourEnumId.startsWith('FT_TYPE_BULK')
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'FundsTransferTypeBehaviour',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.typeBehaviours = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'FundsTransferDebitFieldType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.debitFieldTypes = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'FundsTransferCurrencyFieldType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.currencyFieldTypes = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'FundsTransferChargesFieldType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.chargesFieldTypes = response.data.enumerationList || []
      })
    },
    handleFileUpload () {
      this.templateFile = this.$refs.file.files[0]
    },
    selectTransferTypeRecord (record) {
      this.parentTypeId = record.typeId || ''
    },
    createTransferType (nextAction) {
      let self = this
      let formData = new FormData()
      formData.append('parentTypeId', this.parentTypeId)
      formData.append('typeName', this.typeName)
      formData.append('externalTypeId', this.externalTypeId)
      formData.append('typeBehaviourEnumId', this.typeBehaviourEnumId)
      formData.append('debitFieldTypeEnumId', this.debitFieldTypeEnumId)
      formData.append('currencyFieldTypeEnumId', this.currencyFieldTypeEnumId)
      formData.append('chargesFieldTypeEnumId', this.chargesFieldTypeEnumId)
      formData.append('chargesAmountThreshold', this.chargesAmountThreshold)
      formData.append('inputNotes', this.inputNotes)
      formData.append('reviewNotes', this.reviewNotes)
      formData.append('templateFile', this.templateFile)
      formData.append('position', this.position)
      formData.append('disabled', this.disabled ? 'Y' : 'N')

      this.startLoading('loading-container')
      FundsTransferTypeService.createTransferType({
        formData: formData
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
            self.$refs.wizard.reset()
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewFundsTransferType', params: { id: response.data.typeId } })
          } else {
            self.$router.push({ name: 'BrowseFundsTransferTypes' })
          }
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
